import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    // 季节
    season: 'spring',
    // 用户
    user: {
      name: 'view',
      money: 10000
    },
    // 花盆列表
    pots: [{
      type: 'normal'
    }, {
      type: 'normal'
    }, {
      type: 'normal'
    }, {
      type: 'normal'
    }, {
      type: 'normal'
    }],
    // 植物列表
    plants: [{
      name: '小麦',
      speed: 1000,
      cost: 1,
      profit: 2,
      grows: 'summer',
      image: 'xiaomai.png'
    }, {
      name: '萝卜',
      speed: 1000,
      cost: 3,
      profit: 5,
      grows: 'autumn',
      image: 'luobo.png'
    }, {
      name: '西红柿',
      speed: 1000,
      cost: 10,
      profit: 20,
      grows: 'spring',
      image: 'xihongshi.png'
    }, {
      name: '白菜',
      speed: 1000,
      cost: 5,
      profit: 10,
      grows: 'winter',
      image: 'baicai.png'
    }, {
      name: '土豆',
      speed: 1000,
      cost: 5,
      profit: 10,
      grows: 'autumn',
      image: 'tudou.png'
    }],
    // 当前选中植物
    currPlant: {}
  },
  mutations: {
    // 设置用户名
    setUserName(state, name = "view") {
      state.user.name = name;
    },
    // 改变选中植物
    changePlant(state, payload) {
      state.plants.forEach(obj => {
        if (obj.name === payload) {
          state.currPlant = obj;
        }
      });
    },
    // 添加植物
    setPlant(state, payload) {
      payload.plant = state.currPlant;
      // 扣去成本
      state.user.money -= payload.plant.cost;
    },
    // 删除植物
    clearPlant(state, payload) {
      state.user.money += payload.plant.profit;
      delete payload.plant;
    },
    // 切换下一个季节
    nextSeason(state) {
      let seasonList = ['spring', 'summer', 'autumn', 'winter'];
      let index = seasonList.indexOf(state.season);
      index++;
      if (index === 4) index = 0;
      state.season = seasonList[index];
    }
  },
  getters: {
    seasonCName(state, getters) {
      let season = state.season;
      let seasonMap = {
        'spring': '春天',
        'summer': '夏天',
        'autumn': '秋天',
        'winter': '冬天'
      }
      return seasonMap[season];
    }
  }
})


