<template>
    <div class="plant-picker">
        <div class="nav">
            <Poptip trigger="hover" v-for="plant in plants" :key="plant.name">
                <div class="item" :class="{'active': currPlant.name === plant.name}" @click="onPlantChange(plant.name)">
                    <PlantImage :src="plant.image"></PlantImage>
                    <p>{{plant.name}}</p>
                </div>
                <div class="info" slot="content">
                    <p>植物：{{plant.name}}</p>
                    <p>成本：{{plant.cost}}</p>
                    <p>收益：{{plant.profit}}</p>
                    <p>季节：{{plant.grows | seasonCName}}</p>
                </div>
            </Poptip>
        </div>
    </div>
</template>

<script>
    import { mapState } from 'vuex';
    export default {
        methods: {
            onPlantChange(val) {
                this.$store.commit('changePlant', val);
            }
        },
        computed: mapState(['plants', 'currPlant']),
        filters: {
            seasonCName(season) {
                let seasonMap = {
                    'spring': '春天',
                    'summer': '夏天',
                    'autumn': '秋天',
                    'winter': '冬天'
                }
                return seasonMap[season];
            }
        }
    }
</script>




