<template>
    <div class="plantpot" @mousemove="onPlantpotMouseDown" ref="paper">
        <div v-show="value > 0">
            <ProgressBar :value="value"></ProgressBar>
            <PlantImage :src="pot.plant.image" v-if="pot.plant"></PlantImage>
        </div>
    </div>
</template>

<script>
    import ProgressBar from "@/components/Progress";

    export default {
        components: {
            ProgressBar
        },
        props: {
            pot: Object
        },
        data() {
            return {
                inGrowUp: false,
                value: 0
            }
        },
        methods: {
            onPlantpotMouseDown() {
                console.log(1);
                if (this.pot.plant) return;
                this.$store.commit('setPlant', this.pot);
                if (this.inGrowUp && this.timer || this.value >= 100) return;
                let plant = this.pot.plant;
                this.inGrowUp = true;
                // +1 s
                this.value +1;
                this.timer = setInterval(() => {
                    this.value ++;
                    if(this.value >= 100) {
                        clearInterval(this.timer);
                        this.inGrowUp = false;
                        this.popMoney(plant.profit);
                        this.$store.commit('clearPlant', this.pot);
                        this.value = 0;
                        this.$emit('done');
                    }
                }, plant.speed / 99);
            },
            popMoney(money) {
                let popDom = document.createElement('div');
                popDom.classList.add('pop-money')
                popDom.innerHTML = `￥${money}`;
                this.$refs.paper.appendChild(popDom);
                setTimeout(() => {
                    popDom.remove();
                }, 500)
            }
        }
    }
</script>




